/* reset css start */
@import url('https://fonts.googleapis.com/css2?family=Barlow:wght@300;400;500;600;700&family=Roboto&display=swap');

// font-family: 'Ubuntu', sans-serif;
// font-family: 'Roboto', sans-serif;

$heading-font: 'Barlow', sans-serif;
$para-font: 'Roboto', sans-serif;

html {
  scroll-behavior: smooth; }
body {
  font-family: $para-font;
  color: $para-color;
  font-size: rem(16px);
  padding: 0;
  margin: 0;
  font-weight: 400;
  position: relative;
  line-height: 1.7;
  background: $base-color-two;
  @include transition(all 0.5s);
  overflow-x: hidden;
  &.page-trns-active {
    opacity: 0.5; } }
img {
  max-width: 100%;
  height: auto;
  user-select: none; }

ul, ol {
  padding: 0;
  margin: 0;
  list-style: none; }
button {
  cursor: pointer; }
*:focus {
  outline: none; }
button {
  border: none; }
button:focus {
  outline: none; }
span {
  display: inline-block; }
a:hover {
  color: $base-color; }
/* reset css end */
